import { Component } from '@angular/core';
import { NavController, NavParams, ToastController, LoadingController } from 'ionic-angular';
import { User } from "../../models/user";
import { RegisterPage } from '../register/register';
import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase';
import { ListPage } from '../list/list';
import {Platform} from 'ionic-angular'; 
import { GooglePlus } from '@ionic-native/google-plus';
import { NativeStorage } from '@ionic-native/native-storage';
import { Facebook } from '@ionic-native/facebook';

import { GlobalProvider } from '../../provider/globalprovider';


@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  splash = true;
  user = {
    email: "",
    password: ""
  } as User;

  google = {
    name: '',
    profilePicture: '',
    email: ''
  }
  FB_APP_ID: number = 229144901207708;
  loggedin = false;

  imgHeight:any;

  constructor(private afAuth: AngularFireAuth, 
              private toast: ToastController, 
              public navCtrl: NavController, 
              public loadingCtrl: LoadingController,
              public navParams: NavParams,
              public global: GlobalProvider,
              public platform: Platform,
              public googlePlus: GooglePlus,
              public nativeStorage: NativeStorage,
              public fb: Facebook) {
    this.splash = navParams.get('splash');
    if (this.splash == undefined) {
      this.splash = true;
    }
    
    this.imgHeight = "35%";
    platform.ready().then((readySource) => {
      this.imgHeight = platform.height() * 0.35 + "px";
    });
    // this.fb.browserInit(this.FB_APP_ID, "v2.8");
  }
  
  ngAfterViewInit() {
  }

  ionViewDidLoad() {
    setTimeout(() => this.splash = false, 4000);
  }

  async login(user: User) {
    this.afAuth.auth.signInWithEmailAndPassword(user.email, user.password)
    .then(data => {
      this.navCtrl.setRoot(ListPage);
    })
    .catch (error => {      
      this.toast.create({
        message: 'Could not find authentication details.',
        duration: 5000,
        showCloseButton: false,
        dismissOnPageChange: true,
        cssClass: "toast-failed.scss",
      }).present();
    })   
  }

  logOut() {
    this.afAuth.auth.signOut();
    this.googlePlus.logout().then( () => {
      this.nativeStorage.remove('user');
    },function (error) {
      alert(error);
    });    
    this.googlePlus.disconnect();
    this.loggedin = false;
    this.navCtrl.setRoot(LoginPage);
  }

  signUp() {
    this.navCtrl.push(RegisterPage);
  }

  googleLogin() {   
    let env = this;
    let nav = this.navCtrl;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
    loading.present();	 
    this.googlePlus.login({
      'scopes': '',       
      'webClientId': '402830098374-t0grsirtjuot1sluav5p8ofq52u313ht.apps.googleusercontent.com',
      'offline': true,
      'prompt': 'select_account'
    })
    .then(function (user) {                
      loading.dismiss();      
      env.loggedin = false;
      const googleCredential = firebase.auth.GoogleAuthProvider.credential(user.idToken);
      firebase.auth().signInWithCredential(googleCredential).then( response => {
        console.log(response);        
      });
      env.nativeStorage.setItem('user', {        
        name: user.displayName,
        email: user.email,
        picture: user.imageUrl
      })
      .then(function(){        
        nav.setRoot(ListPage);
        }, function (error) {
        alert(error);
      })
    }, function (error) {
      alert(error);
      loading.dismiss();
    });
  }

  facebookLogin() {
    let permissions = new Array<string>();
    let nav = this.navCtrl;
	  let env = this;   
    permissions = ["public_profile"];
    this.fb.login(permissions)
    .then(function(response){      
      let userId = response.authResponse.userID;
      let params = new Array<string>();
     
      env.fb.api("/me?fields=name,gender", params)
      .then(function(user) {
        user.picture = "https://graph.facebook.com/" + userId + "/picture?type=large";        
        env.nativeStorage.setItem('user',
        {
          name: user.name,
          gender: user.gender,
          picture: user.picture
        })
        .then(function(){
          nav.setRoot(ListPage);
        }, function (error) {
          console.log(error);
        })
      })
    }, function(error){
      console.log(error);
    });
  }
}



